import { LCurly, RCurly } from "../../lexer/control.js";
import { OutputToken } from "../../lexer/query.js";
import { NewlineToken } from "../../lexer/tokens.js";

/**
 * @param {import('../base_parser.js').XanoBaseParser} $
 */
export function outputClause($) {
  return () => {
    $.sectionStack.push("outputClause");
    $.CONSUME(OutputToken); // "output"
    // unlike schema, output can be mentioned without any fields
    $.OPTION(() => {
      $.CONSUME(LCurly); // "{"
      $.MANY(() => {
        $.AT_LEAST_ONE1(() => $.CONSUME1(NewlineToken)); // at least one new line
        $.OR([
          { ALT: () => $.SUBRULE($.objectColumnDefinition) },
          { ALT: () => $.SUBRULE($.enumColumnDefinition) },
          {
            ALT: () =>
              $.SUBRULE($.columnDefinition, {
                ARGS: [{ include_file: false }],
              }),
          },
        ]);
      });
      $.AT_LEAST_ONE2(() => $.CONSUME2(NewlineToken)); // at least one new line
      $.CONSUME(RCurly); // "}"
    });
    $.sectionStack.pop();
  };
}
